// Breakpoints

@mixin respond-to($breakpoint) {
  @if $breakpoint == "ex-small-up" {
    @media (min-width: 300px) {
      @content;
    }
  }

  @else if $breakpoint == "small-up" {
    @media (min-width: 600px) {
      @content;
    }
  }

  @else if $breakpoint == "medium-up" {
    @media (min-width: 900px) {
      @content;
    }
  }

  @else if $breakpoint == "large-up" {
    @media (min-width: 1200px) {
      @content;
    }
  }
  @else if $breakpoint == "ex-large-up" {
    @media (min-width: 1600px) {
      @content;
    }
  }
  @else if $breakpoint == "ex-small-down" {
    @media (max-width: 299px) {
      @content;
    }
  }

  @else if $breakpoint == "small-down" {
    @media (max-width: 599px) {
      @content;
    }
  }

  @else if $breakpoint == "medium-down" {
    @media (max-width: 899px) {
      @content;
    }
  }

  @else if $breakpoint == "large-down" {
    @media (max-width: 1199px) {
      @content;
    }
  }
  @else if $breakpoint == "ex-large-down" {
    @media (max-width: 1599px) {
      @content;
    }
  }
}
#accordion {
  margin: 1rem 0;
}

.ui-corner-all,
.ui-corner-top,
.ui-corner-right,
.ui-corner-tr,
.ui-corner-left,
.ui-corner-tl,
.ui-corner-bottom,
.ui-corner-br,
.ui-widget-content {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius:0;
  border-color: var(--color-shadow-gray);
}

.ui-state-default,
.ui-widget-content
.ui-state-default,
.ui-widget-header
.ui-state-default {
  background: var(--color-light-gray);
  color: var(--color-text-gray);
}

.ui-widget {
  font: inherit;
}


#tabs {
  border: none;
}

.ui-widget-header {
  background: var(--color-white);
}

.ui-tabs .ui-tabs-nav {
  border-top: none;
  border-right: none;
  border-left: none;
  padding: 0;
}

.ui-tabs .ui-tabs-nav li {
  margin-right: .5em;
}

.ui-tabs .ui-tabs-panel {
  border-width: 1px;
  border-top: none;
  background: white;
}

.ui-tabs .ui-tabs-nav li.ui-tabs-active {
  background: white;
}

.ui-tabs .ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active {
  border-color: var(--color-shadow-gray);
}

.ui-widget-content a {
  color: var(--color-primary);
}

.ui-widget-content a.button {
  color: var(--button-color);
}

.ui-state-active a, .ui-state-active a:link, .ui-state-active a:visited {
    color: inherit;
    text-decoration: none;
}

.ui-state-active a, .ui-state-active a:link, .ui-state-active a:visited {
  color: var(--color-text-gray);
}

.ui-widget-content {
  color: var(--color-text-gray);
}

.ui-state-active a, .ui-state-active a:link, .ui-state-active a:visited {
  color: #333;
}

/*! Badges */

.badge {
  font-size: var(--badge-font-size);
  font-weight: var(--badge-font-weight);
  color: var(--badge-color);
  background-color: var(--badge-bg-color-primary);
  border-radius: var(--badge-border-radius);
  padding: var(--badge-spacing-y) var(--badge-spacing-x);
  vertical-align: baseline;
  display: inline-block;
}

.badge:empty {
  display: none;
}

.badge-success { background-color: var(--badge-bg-color-success); }
.badge-info { background-color: var(--badge-bg-color-info); }
.badge-warning { background-color: var(--badge-bg-color-warning); }
.badge-danger { background-color: var(--badge-bg-color-danger); }
.badge-inverse { background-color: var(--badge-bg-color-inverse); }

.block {
  margin-bottom: 1.5em;
}
/*! Content */

/* Box sizing reset */
html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/***************************************************************************************************
* The basics
***************************************************************************************************/

body {
  background: var(--body-bg-color);
  font-family: var(--font-sans-serif);
  font-weight: var(--font-weight);
  font-size: var(--font-size);
  color: var(--body-color);
  line-height: var(--line-height);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-stroke: 0.45px rgba(0, 0, 0, 0.1);
}

a {
  color: var(--link-color);
  text-decoration: var(--link-text-decoration);
}

a:hover {
  color: var(--link-color-hover);
  text-decoration: var(--link-text-decoration-hover);
}

abbr[title] {
  text-decoration: none;
  border-bottom: dashed 2px currentcolor;
}

b, strong {
  font-weight: var(--font-weight-bold);
}

big {
  font-size: var(--font-size-big);
}

blockquote {
  font-size: var(--font-size-big);
  border-left: solid .4rem var(--component-border-color);
  padding: .5rem .5rem .5rem 1rem;
  margin-bottom: 1rem;
  margin-left: 0;
}

blockquote :last-child {
  margin-bottom: 0;
}

code {
  font-family: var(--font-monospace);
  font-size: var(--code-font-size);
  color: var(--code-color);
  background-color: var(--code-bg-color);
  border-radius: var(--code-border-radius);
  padding: var(--code-spacing-y) var(--code-spacing-x);
}

del {
  color: var(--color-red);
}

figure {
  margin-top: 0;
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--headings-font-family);
  font-weight: var(--headings-font-weight);
  line-height: var(--headings-line-height);
  color: var(--headings-color);
  margin-top: 0;
  margin-bottom: var(--headings-margin-bottom);
}

h1 { font-size: var(--headings-font-size-h1); }
h2 { font-size: var(--headings-font-size-h2); }
h3 { font-size: var(--headings-font-size-h3); }
h4 { font-size: var(--headings-font-size-h4); }
h5 { font-size: var(--headings-font-size-h5); }
h6 { font-size: var(--headings-font-size-h6); }

hr {
  border: none;
  border-top: solid var(--hr-border-width) var(--hr-border-color);
  margin: var(--hr-spacing) 0;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

ins {
  color: var(--color-green);
}

kbd {
  font-family: var(--font-monospace);
  font-size: var(--kbd-font-size);
  color: var(--kbd-color);
  background-color: var(--kbd-bg-color);
  border-radius: var(--kbd-border-radius);
  padding: var(--kbd-spacing-y) var(--kbd-spacing-x);
}

mark {
  color: var(--mark-color);
  background-color: var(--mark-bg-color);
  padding: var(--mark-spacing-y) var(--mark-spacing-x);
}

p {
  margin-top: 0;
  margin-bottom: .5rem;
}

pre {
  max-height: var(--pre-max-height);
  overflow-y: auto;
  font-family: var(--font-monospace);
  color: var(--pre-color);
  background-color: var(--pre-bg-color);
  border-radius: var(--pre-border-radius);
  padding: 1rem;
  margin-top: 0;
  margin-bottom: 1rem;
}

small {
  font-size: var(--font-size-small);
}

/***************************************************************************************************
* Lists
***************************************************************************************************/

dl {
  margin-bottom: .5rem;
}

dt {
  font-weight: var(--font-weight-bold);
}

dd {
  margin-left: 0;
  margin-bottom: .5rem;
}

ol,
ul {
  line-height: var(--line-height);
  margin-top: 0;
  margin-bottom: .5rem;
}

ol ol,
ol ul,
ul ol,
ul ul {
  margin-bottom: 0;
}

/***************************************************************************************************
* Placeholders
***************************************************************************************************/

::-webkit-input-placeholder { color: var(--placeholder-color); }
::-moz-placeholder { color: var(--placeholder-color); }
:-ms-input-placeholder { color: var(--placeholder-color); }
:-moz-placeholder { color: var(--placeholder-color); }

/***************************************************************************************************
* Selections
***************************************************************************************************/

::-moz-selection {
  background-color: var(--selection-bg-color);
  color: var(--selection-color);
  text-shadow: none !important;
}

::selection {
  background-color: var(--selection-bg-color);
  color: var(--selection-color);
  text-shadow: none !important;
}

/*! Dropdowns */

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-trigger::after {
  content: '▼';
  font-family: var(--font-system);
  font-size: .6em;
  vertical-align: middle;
  margin-left: .5em;
  display: inline-block;
  transform: scaleY(.75);
}

.dropdown-top .dropdown-trigger::after {
  transform: scaleY(.75) rotate(180deg);
}

.dropdown-menu {
  position: absolute;
  top: calc(100% + var(--dropdown-offset-y));;
  left: var(--dropdown-offset-x);
  z-index: var(--dropdown-z-index);
  min-width: var(--dropdown-min-width);
  max-width: var(--dropdown-max-width);
  max-height: var(--dropdown-max-height);
  background-color: var(--dropdown-bg-color);
  border: solid var(--dropdown-border-width) var(--dropdown-border-color);
  border-radius: var(--dropdown-border-radius);
  box-shadow: var(--dropdown-box-shadow);
  display: none;
  padding: calc(var(--dropdown-spacing-y) / 2) 0;
  overflow-y: auto;
}

.dropdown-top .dropdown-menu {
  top: auto;
  bottom: calc(100% + var(--dropdown-offset-y));;
}

.dropdown-left .dropdown-menu {
  left: auto;
  right: var(--dropdown-offset-x);
}

.dropdown.active .dropdown-menu {
  display: block;
}

.dropdown-menu a {
  position: relative;
  color: var(--dropdown-color);
  text-decoration: none;
  padding: var(--dropdown-spacing-y) calc(var(--dropdown-spacing-x) + .5em);
  display: block;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

.dropdown-menu a:hover:not(.disabled) {
  color: var(--dropdown-color-hover);
  background-color: var(--dropdown-bg-color-hover);
}

.dropdown-menu a.disabled {
  cursor: not-allowed;
  opacity: .5;
}

.dropdown-menu a.checked::before {
  position: absolute;
  left: calc(var(--dropdown-spacing-x) / 2 - .1em);
  content: '✓';
  font-family: var(--font-system);
  font-weight: 600;
}

.dropdown-menu hr {
  width: 100%;
  border-top: solid var(--dropdown-divider-border-width) var(--dropdown-divider-border-color);
  margin: calc(var(--dropdown-spacing-y) / 2) 0;
}

/*! Buttons */

button,
.button {
  font-family: var(--button-font-family);
  font-size: var(--button-font-size);
  font-weight: var(--button-font-weight);
  text-align: center;
  text-decoration: none;
  color: var(--button-color);
  background-color: var(--button-bg-color-primary);
  border-radius: var(--button-border-radius);
  border: none;
  height: var(--input-height);
  line-height: 1;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  padding: calc(var(--component-spacing) * .5) calc(var(--component-spacing) * .75);
  box-shadow: var(--button-box-shadow);
  cursor: pointer;
  display: inline-block;
  transition: .1s box-shadow, .1s background-color, .1s color;
}

.button-small {
  font-size: calc(var(--button-font-size) * .8);
  height: var(--input-height-small);
  padding: calc(var(--component-spacing) * .25) calc(var(--component-spacing) * .5);
}

.button-big {
  font-size: calc(var(--button-font-size) * 1.2);
  height: var(--input-height-big);
  padding: calc(var(--component-spacing) * .75) calc(var(--component-spacing) * 1);
}

.button-block {
  width: 100% !important;
  display: block;
}

button:hover,
.button:hover {
  color: var(--button-color);
  text-decoration: none;
}

button:hover:not(:disabled),
.button:hover:not(.disabled) {
  box-shadow: var(--button-box-shadow-hover);
}

button:active:not(:disabled),
button.active:not(:disabled),
.button.active:not(.disabled) {
  color: var(--button-color);
  box-shadow: var(--button-box-shadow-active);
}

button:disabled,
button.disabled,
.button.disabled {
  opacity: .5;
  cursor: not-allowed;
}

.button-success, .button-green { background-color: var(--button-bg-color-success); }
.button-info, .button-light-blue { background-color: var(--button-bg-color-info); }
.button-warning, .button-orange { background-color: var(--button-bg-color-warning); }
.button-danger, .button-red { background-color: var(--button-bg-color-danger); }
.button-inverse, .button-black { background-color: var(--button-bg-color-inverse); }
.button-yellow { background-color: var(--color-yellow); }


.button-link, button.link {
  background-color: transparent;
  color: var(--link-color);
  text-decoration: var(--link-text-decoration);
  box-shadow: none;
}

.button-link:hover:not(:disabled), button.link:hover:not(:disabled) {
  background-color: transparent;
  color: var(--link-color-hover);
  text-decoration: var(--link-text-decoration-hover);
  box-shadow: none;
}

label.button input[type="file"] {
  display: none;
}

input#edit-supplies-add-more-items {
  width: auto;
  display: inline-block;
}

.button--round {
  box-sizing: border-box;
  display: inline-block;
  position: relative;
  text-align: center;
  line-height: normal;
  cursor: pointer;
  padding: .25em 1.5em;
  border: 1px solid var(--color-shadow-gray);
  border-radius: 1em;
  background-color: var(--color-light-gray);
  color: var(--color-text-gray);
  text-decoration: none;
  font-weight: 600;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  -webkit-font-smoothing: antialiased;
  &:hover {
    background-color: var(--color-shadow-gray);
   }
}

/*! Forms */

fieldset {
  border: solid var(--fieldset-border-width) var(--fieldset-border-color);
  border-radius: var(--fieldset-border-radius);
  padding: var(--component-spacing) calc(var(--component-spacing) * 1.5);
  margin-bottom: 1rem;
}

fieldset legend {
  font-weight: var(--font-weight-bold);
  padding: 0 .25rem;
}

label {
  display: inline-block;
  margin-bottom: .25rem;
}

label + label {
  margin-left: 1rem;
}

input[type="color"],
input[type="date"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select,
textarea {
  width: calc( 100% - var(--component-spacing));
  font-family: var(--input-font-family);
  font-size: var(--input-font-size);
  font-weight: var(--input-font-weight);
  color: var(--input-color);
  border: solid var(--input-border-width) var(--input-border-color);
  border-radius: var(--input-border-radius);
  box-shadow: var(--input-box-shadow);
  background-color: var(--input-bg-color);
  height: var(--input-height);
  line-height: var(--input-height);
  vertical-align: middle;
  display: block;
  padding-left: calc(var(--component-spacing) * .5);
  padding-right: calc(var(--component-spacing) * .5);
  margin: 0;
  transition: .1s border-color, .1s background-color, .1s color;
  white-space: nowrap;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input[type="color"]:focus,
input[type="date"]:focus,
input[type="datetime-local"]:focus,
input[type="email"]:focus,
input[type="month"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="time"]:focus,
input[type="url"]:focus,
input[type="week"]:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: var(--input-border-color-focus);
}

select {
  position: relative;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='41px' height='26px' viewBox='0 0 41 26' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cdefs%3E%3C/defs%3E%3Cg id='Page-1' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cpolygon id='Path-3' fill='%23000000' points='0 5.38215461 19.9830489 25.3652035 40.1398855 5.20836689 34.9315186 0 19.8691842 15.0623344 4.83971338 0.0328636246'%3E%3C/polygon%3E%3C/g%3E%3C/svg%3E%0A");
  background-position: right .4rem center;
  background-repeat: no-repeat;
  background-size: .75rem;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: calc(var(--component-spacing) * .5 + 1rem);
}

textarea {
  height: auto;
  resize: vertical;
  line-height: var(--line-height);
  white-space: normal;
}

input[type="color"] {
  padding: calc(var(--component-spacing) * .5);
}

input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}

input[type="color"]::-webkit-color-swatch {
  border: none;
  border-radius: var(--input-border-radius);
}

input[type="checkbox"],
input[type="radio"] {
  padding: 0;
}

input[disabled] {
  opacity: .5;
  cursor: not-allowed;
}

input[readonly] {
  background-color: var(--input-readonly-bg-color);
}

input.input-small,
select.input-small {
  font-size: var(--input-font-size-small);
  height: var(--input-height-small);
  line-height: var(--input-height-small);
}

textarea.input-small {
  font-size: var(--input-font-size-small);
}

input.input-big,
select.input-big {
  font-size: var(--input-font-size-big);
  height: var(--input-height-big);
  line-height: var(--input-height-big);
}

textarea.input-big {
  font-size: var(--input-font-size-big);
}

/* Range styles courtesy of http://danielstern.ca/range.css/ */
input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  margin: 1rem 0;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: .5rem;
  cursor: pointer;
  background: var(--input-range-track-color);
  border-radius: var(--input-border-radius);
  border: none;
  box-shadow: var(--input-range-track-box-shadow);
}

input[type="range"]::-webkit-slider-thumb {
  border: none;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background: var(--input-range-thumb-color);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -.5rem;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: var(--input-range-track-color);
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: .5rem;
  cursor: pointer;
  background: var(--input-range-track-color);
  border-radius: var(--input-border-radius);
  border: none;
  box-shadow: var(--input-range-track-box-shadow);
}

input[type="range"]::-moz-range-thumb {
  border: none;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 50%;
  background: var(--input-range-thumb-color);
  cursor: pointer;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: .5rem;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type="range"]::-ms-fill-lower {
  background: #2f6ea5;
  border: none;
  border-radius: var(--input-border-radius);
}

input[type="range"]::-ms-fill-upper {
  background: var(--input-range-track-color);
  border: none;
  border-radius: var(--input-border-radius);
}

input[type="range"]::-ms-thumb {
  border: none;
  height: .5rem;
  width: .5rem;
  border-radius: 50%;
  background: var(--input-range-thumb-color);
  cursor: pointer;
}

input[type="range"]:focus::-ms-fill-lower {
  background: var(--input-range-track-color);
}

input[type="range"]:focus::-ms-fill-upper {
  background: var(--input-range-track-color);
}

/* Progress bars */
progress {
  width: 100%;
  vertical-align: middle;
}

/* Input fields */
.input-single {
  margin-bottom: 1rem;
}

/* Input groups */
.input-group {
  width: 100%;
  display: flex;
  margin-bottom: 1rem;
}

.input-group > input,
.input-group > button,
.input-group > .button,
.input-group > .dropdown button,
.input-group > .dropdown .button {
  border-radius: 0;
}

.input-group > input:first-child,
.input-group > button:first-child,
.input-group > .button:first-child,
.input-group > .dropdown:first-child button,
.input-group > .dropdown:first-child .button {
  border-top-left-radius: var(--input-border-radius);
  border-bottom-left-radius: var(--input-border-radius);
}

.input-group > input:last-child,
.input-group > button:last-child,
.input-group > .button:last-child,
.input-group > .dropdown:last-child button,
.input-group > .dropdown:last-child .button {
  border-top-right-radius: var(--input-border-radius);
  border-bottom-right-radius: var(--input-border-radius);
}

.input-group > input + input {
  border-left-width: 0;
}

.input-group > input + input:focus {
  border-left-width: var(--input-border-width);
  margin-left: calc(var(--input-border-width) * -1);
}

.input-group :focus {
  /* Force a new stacking context so focus rings don't get truncated by adjacent elements */
  transform: translateZ(0);
}

/* Input addons */
.input-addon {
  color: var(--text-muted);
  height: var(--input-height);
  line-height: var(--input-height);
  border-top: solid var(--input-border-width) var(--input-border-color);
  border-bottom: solid var(--input-border-width) var(--input-border-color);
  background: #f8f8f8;
  padding: 0 calc(var(--component-spacing) * .5);
  white-space: nowrap;
}

.input-addon-small {
  font-size: var(--input-font-size-small);
  height: var(--input-height-small);
  line-height: var(--input-height-small);
}

.input-addon-big {
  font-size: var(--input-font-size-big);
  height: var(--input-height-big);
  line-height: var(--input-height-big);
}

.input-addon:first-child {
  border-left: solid var(--input-border-width) var(--input-border-color);
  border-top-left-radius: var(--input-border-radius);
  border-bottom-left-radius: var(--input-border-radius);
}

.input-addon:last-child {
  border-right: solid var(--input-border-width) var(--input-border-color);
  border-top-right-radius: var(--input-border-radius);
  border-bottom-right-radius: var(--input-border-radius);
}

/* Validation */
.input-invalid label {
  color: var(--input-invalid-color) !important;
}

.input-invalid,
.input-invalid input,
.input-invalid select {
  color: var(--input-invalid-color) !important;
  border-color: var(--input-invalid-border-color) !important;
}

.input-valid label {
  color: var(--input-valid-color) !important;
}

.input-valid,
.input-valid input,
.input-valid select {
  color: var(--input-valid-color) !important;
  border-color: var(--input-valid-border-color) !important;
}

