    margin:0px auto;
  }
}

.field--name-field-sc-video {
  position: relative;
  height: 100%;
  width: 100%;
  margin-bottom: 2em;
  iframe {
    width: 250px;
    height: 141px;
    max-width: 100%;
    @include respond-to(small-up) {
      width: 300px;
      height: 169px;
    }
    @include respond-to(medium-up) {
      width: 600px;
      height: 337px;
    }
    @include respond-to(large-up) {
      width: 854px;
      height: 480px;
    }
  }
}

.paragraph--type--_-column .paragraph-sections>div.field--name-field-sc-video,
.paragraph--type--_-column .paragraph--type--video .paragraph-sections>div.field--name-field-sc-text {
  flex-basis: 49%;
  margin-right: 2%;
  max-width: 49%;
  @include respond-to(medium-down) {
    flex-basis: 100%;
    max-width: 100%;
    margin-right: 0;
  }
}

.paragraph--type--_-column .paragraph--type--video .paragraph-sections>div.field--name-field-sc-text {
  margin-right: 0;
  padding-top: 0;
}
/*! Alerts */

.alert {
  color: var(--alert-color);
  background-color: var(--alert-bg-color-primary);
  border-radius: var(--alert-border-radius);
  padding: var(--alert-spacing-y) var(--alert-spacing-x);
  margin-top: 0;
  margin-bottom: 1rem;
}

.alert:empty {
  display: none;
}

.alert-success { background-color: var(--alert-bg-color-success); }
.alert-info { background-color: var(--alert-bg-color-info); }
.alert-warning { background-color: var(--alert-bg-color-warning); }
.alert-danger { background-color: var(--alert-bg-color-danger); }
.alert-inverse { background-color: var(--alert-bg-color-inverse); }

.alert a {
  color: inherit;
  text-decoration: underline;
}

.alert :last-child {
  margin-bottom: 0;
}
